body,
html {
  overflow: hidden;
  overscroll-behavior: none;
  position: fixed;
  height: 100vh;
  width: 100vw;
}
.guiControls {
  color: white;
  font-family: Montserrat;
  font-weight: 100;
  font-size: 12px;
  position: absolute;
  bottom: 0;
  right: 12px;
}
.guiControls_item {
  display: flex;
  align-content: center;
  margin-bottom: 12px;
}
.actionName {
  align-self: center;
}
.buttonName {
  padding: 4px;
  border-radius: 5px;
  background: rgba(255, 255, 255, 0.3);
  margin-right: 5px;
}
.menu {
  opacity: 0;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000000;
}
.menu_wrapper {
  height: 100%;
  width: 100%;
  position: relative;
}
.menu_content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.menu_game-name {
  font-family: Montserrat;
  font-weight: 800;
  font-size: 170px;
  color: white;
  display: block;
  position: relative;
}
.menu_domain {
  font-size: 22px;
  font-weight: 600;
  position: absolute;
  bottom: 15px;
  right: 5px;
  opacity: 0.8;
}
.menu_play {
  color: white;
  font-family: Montserrat;
  font-weight: 300;
  font-size: 30px;
  padding: 15px 35px;
  border-radius: 5px;
  display: inline-block;
  background: rgba(255, 255, 255, 0.24);
  cursor: pointer;
  transition: background 0.3s ease;
}
.menu_play:hover {
  background: #ffc341;
}
.menu_bestScore {
  color: white;
  font-family: Montserrat;
  font-weight: 300;
  margin-top: 15px;
  font-size: 16px;
  opacity: 0.7;
}
.menu_mail {
  color: white;
  position: absolute;
  bottom: 20px;
  right: 20px;
  font-family: Montserrat;
  font-size: 14px;
  opacity: 0.5;
}
.mobile {
  display: none;
  height: 100vh;
  width: 100vw;
  position: absolute;
  z-index: 10000000;
  background: #21bfe1;
  color: white;
  font-family: Montserrat;
  text-align: center;
}
.mobile__true {
  display: block;
}
.mobile_content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.mobile_text {
  font-size: 18px;
}
.mobile_text__big {
  font-size: 100px;
  font-weight: 700;
}
.dg.ac {
  z-index: 1000000000!important;
}
